import axios from '@/utils/uniAxios.js'
export default {
	data() {
		return {
			time:'',
			form: {
				collect: [{
					time: '2019-04-06 11:37',
					shopName: '麦田圈官网旗舰店',
					state: 0,
					goodsList: [{
						title: '香辣牛肉干',
						price: 88.88,
						image: '/static/goods.png',
						number: 1,
						attr: '规格 10*200g'
					}]
				}],
				goodsObj: {
					remarks: '香辣味的',
					allPay: '120',
					jifen: '100',
					preferential: '20.00',
					total: '880'
				},
				orderMsg: {
					number: '13245679851354664',
					num: '3212313213213213132',
					creatTime: '2019-11-05 11:12:12',
					payTime: '2019-11-05 11:12:12',
					payWay: '微信支付'
				}
			},
			img: [
				'icon-daifahuo',
				'icon-daifukuan1',
				'icon-daishouhuo1',
				'icon-chenggong',
				'icon-dingdanguanbi',
				'icon-dingdanwancheng'
			],
			order: {
				status: '',
				img: '',
				wuliu: '',
				pay: '',
				statusMsg: '',
				color: ''
			},
			statuss: '',
			show: false,
			marginBottom: '',
			steps: [{
					text: '步骤一',
					desc: '描述信息'
				},
				{
					text: '步骤二',
					desc: '描述信息'
				},
				{
					text: '步骤三',
					desc: '描述信息'
				},
				{
					text: '步骤四',
					desc: '描述信息'
				}
			],
			id: '',
			wuliuShow:true
		}
	},
	onLoad(option) {
		console.log(option)
		this.statuss = option.status;
		this.id = option.id;
		this.getOrder()
	},
	methods: {
		// 获取订单详情
		getOrder() {
			let obj = {
				cartItemIds: [this.id]
			}
			axios.post('/order/getDetailByOrderId', {
				id: this.id
			}).then(res => {
				if (res.data.code == '200') {
					this.order = res.data.data;
					console.log(this.order)
					if (this.order.status == 0) {
						// 待支付
						this.show = true;
						this.wuliuShow = true;
						this.order.statusMsg = '';
						this.order.wuliu = '取消订单';
						this.order.img = this.img[1];
						this.order.pay = '立即支付';
						this.order.color = ''
					} else if (this.order.status == 1) {
						//待发货	
						this.show = false;

						this.order.statusMsg = '待发货';
						this.order.wuliu = '';
						this.order.img = this.img[0];
						this.order.pay = '';
						this.order.color = '#F7B62C'
					} else if (this.order.status == 2) {
						//待收货
						this.show = true;

						this.order.statusMsg = '待收货';
						// this.order.wuliu = '查看物流';
						this.order.wuliu = '';
						this.wuliuShow = false;
						this.order.img = this.img[2];
						this.order.pay = '确认收货';
						this.order.color = '#515151'
					} else if (this.order.status == 6) {
						//交易成功
						this.show = true;
						this.order.statusMsg = '交易成功';
						this.order.wuliu = '删除订单';
						this.order.img = this.img[3];
						// this.order.pay='去评价';//评价不做
						this.order.color = '#01B300'
					} else if (this.order.status == 4) {
						// 交易关闭
						this.show = true;
						this.order.wuliu = '删除订单';
						this.order.img = this.img[4]
					} else if (this.order.status == 3) {
						// 已完成
						this.show = true;
						this.order.statusMsg = '已完成';
						this.order.wuliu = '删除订单';
						this.order.img = this.img[5];
						this.order.pay = '再次购买';
						this.order.color = '#515151'
					}

				}
			})
		},
		consult() {
			uni.navigateTo({
				url: 'consult'
			})
		},
		// 根据不同状态，跳转或请求不同的页面
		logisticsTap(id) {
			console.log(this.statuss)
			if (this.order.status == 2) { //待收货
				uni.navigateTo({
					url: 'logistics'
				})
			} else if (this.statuss == 7 || this.order.status == 4 || this.statuss == 6) { //已完成，交易关闭，交易成功的订单，就删除订单
				axios.post('/order/deleteOrder', {
					id: this.order.id
				}).then(res => {
					console.log(res)
					if (res.data.code == '200') {
						this.$api.msg('删除成功');
						uni.navigateTo({
							url: 'order' //返回到订单详情
						})
					}
				})
			} else if (this.order.status == 1) { //待付款，取消订单
				axios.post('/order/cancelOrder', {
					orderId: id
				}).then(res => {
					if (res.data.code == 200) {
						this.$api.msg('订单取消成功')
						uni.redirectTo({
							url: 'order' //返回到订单详情
						})
					}
				})
			}
		},
		pay(id, productId) {
			if (this.order.status == 2) { // 确认收货
				axios.post('/order/updateOrderStatus', {
					orderId: id,
					status: 2
				}).then(res => {
					console.log(res)
					if(res.data.code==200){
						uni.navigateTo({
							url: 'order?state=3'
						})
					}
				})
			} else if (this.order.status == 3) { //再次购买
				uni.navigateTo({
					url: '../product/product?id=' + productId
				})
			} else if (this.order.status == 0) { //立即支付
				let code = uni.getStorageSync('code')
				let obj = {
					code: code, //code
					orderSn: this.order.orderSn, //订单编号orderSn
					payType: 3, //支付类型
					rechargeMoney: this.order.payAmount //支付金额s
				}
				let _this = this;
				axios.post('/pay/payOrder', obj).then(res => {
					console.log(res)
					_this.payCode = {
						appId: res.data.data.appId,
						nonceStr: res.data.data.nonceStr,
						package: res.data.data.package,
						paySign: res.data.data.paySign,
						signType: "MD5",
						timeStamp: res.data.data.timeStamp,
					}
					uni.requestPayment({
						provider: 'wxpay',
						timeStamp: _this.payCode.timeStamp,
						nonceStr: _this.payCode.nonceStr,
						package: _this.payCode.package,
						signType: 'MD5',
						paySign: _this.payCode.paySign,
						success: function(res) {
							console.log('success:' + JSON.stringify(res));
							uni.reLaunch({
								url: 'paySuccess?totalCount=' + _this.totalCount + '&id=' + _this.orderList.id
							})
						},
						fail: function(err) {
							console.log('fail:' + JSON.stringify(err));
							this.$api.msg('取消付款')
						}
					})
				})
			}
		},
		// 日期转换
		// 时间戳转换成时间
		timestampToTime(time) {
			var date = new Date(time) //时间戳为10位需*1000，时间戳为13位的话不需乘1000
			var Y = date.getFullYear()
			var M = (date.getMonth() + 1 < 10 ? '0' + (date.getMonth() + 1) : date.getMonth() + 1)
			var D = date.getDate()
			var h = date.getHours() + ':'
			var m = date.getMinutes() + ':'
			var s = date.getSeconds();

			return Y + '-' + M + '-' + D + ' ' + h + m + s
		}
	}
}
